@import 'icons';

@font-face {
    font-family: 'WonouIco';
    src: url('fonts/WonouIco.eot') format('eot');
    src: url('fonts/WonouIco.ttf') format('truetype'),
    url('fonts/WonouIco.woff') format('woff'),
    url('fonts/WonouIco.svg') format('svg');
}

.icon{
    font-family: 'WonouIco';
}

@mixin icon($icon-name, $icon-number){
    .icon-#{$icon-name}::before{
        content: $icon-number;
    }
}

@each $icon in $icons{
    @include icon(map-get($icon, 'name'), map-get($icon, 'number'));
}


